import React, { Component } from 'react';
import styles from './index.css';

class Title extends Component {
  render() {
    return (
      <div className={styles.root}>
      	<h1 className={ styles.title + ' -underline' }>{ this.props.title }</h1>
      </div>
    );
  }
}

export default Title;



